/* eslint-disable react/no-danger */
import { formatDate } from 'maeng-utils/utils'
import { PageProps, graphql } from 'gatsby'
import React, { useCallback, useMemo } from 'react'
import { BASE_LINE, INTERSECTION_OBSERVER_ROOT_MARGIN } from '@/constants'
import useIntersectionObserver from '@/hooks/useIntersectionObserver'
import useRenderedCount from '@/hooks/useRenderedCount'
import useScrollEvent from '@/hooks/useScrollEvent'
import { addClass, getDocumentHeight } from '@/utils/dom'
import { toFit } from '@/utils/event-manager'

interface TilPageDataProps {
  site: {
    siteMetadata: {
      configs: {
        countOfInitialPost: number
      }
    }
  }
  allMarkdownRemark: {
    edges: {
      node: {
        frontmatter: {
          date: string
          title: string
        }
        fields: {
          slug: string
        }
        html: string
      }
    }[]
  }
}

const TILPage = ({ data }: PageProps<TilPageDataProps>) => {
  const { countOfInitialPost } = data.site.siteMetadata.configs
  const tilList = data.allMarkdownRemark.edges
  const [count, countRef, increaseCount] = useRenderedCount()

  const getDistance = useCallback((currentPos: number) => {
    return getDocumentHeight() - currentPos
  }, [])

  useIntersectionObserver(
    (entries: IntersectionObserverEntry[]) => {
      return entries
        .filter(({ isIntersecting }) => isIntersecting)
        .forEach(({ target }) => addClass(target as HTMLElement, 'observed-shown'))
    },
    {
      rootMargin: INTERSECTION_OBSERVER_ROOT_MARGIN,
    },
  )

  useScrollEvent(() => {
    const currentPos = window.scrollY + window.innerHeight
    const isTriggerPos = () => getDistance(currentPos) < BASE_LINE
    const doesNeedMore = () => tilList.length > countRef.current * countOfInitialPost

    return toFit(increaseCount, {
      dismissCondition: () => !isTriggerPos(),
      triggerCondition: () => isTriggerPos() && doesNeedMore(),
    })()
  })

  const refinedTils = useMemo(() => tilList.slice(0, count * countOfInitialPost), [count, tilList])

  return (
    <div className="flex flex-col flex-1 gap-10 w-[50%] my-8 mx-auto">
      {refinedTils.map(({ node }) => (
        <div
          key={node.frontmatter.date}
          className="observed observed-hidden mockup-browser border border-base-300"
        >
          <div
            className="mockup-browser-toolbar before:!shadow-[1.4em_0_rgb(248,_113,_113),_2.8em_0_rgb(74,_222,_128),_4.2em_0_rgb(250,_204,_21)] before:!opacity-100
    "
          >
            <div className="input border border-base-300 !outline-none !w-full">
              {node.frontmatter.title}
            </div>
          </div>

          <div className="flex flex-col px-4 py-2 border-t border-base-300">
            <small className="text-gray-500 text-right mb-2">
              {formatDate(new Date(node.frontmatter.date), 'YYYY.MM.DD (ddd)')}
            </small>
            <p
              className="min-h-[150px] max-h-[150px] overflow-auto"
              dangerouslySetInnerHTML={{ __html: node.html }}
            />
          </div>
        </div>
      ))}
    </div>
  )
}

export const pageQuery = graphql`
  query TilQuery {
    site {
      siteMetadata {
        configs {
          countOfInitialPost
        }
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { fileAbsolutePath: { regex: "//til//" } }
    ) {
      edges {
        node {
          frontmatter {
            title
            date(formatString: "YYYY-MM-DD HH:mm:ss")
          }
          fields {
            slug
          }
          html
        }
      }
    }
  }
`

export default TILPage
